body {
  margin: 0;
  padding: 0;
font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.side-nav{
	padding:30px;
	height:350px;
	background:var(--color-two);
	box-shadow:1px 0px 7px rgba(0,0,0,0.5);
	position:fixed;
	top:0;
	right:0;
	width:70%;
	max-width:300px;
	width:300px;
	z-index:300;
	transition: ease-out, -webkit-transform 0.5s;
	transition: transform 0.5s, ease-out;
	transition: transform 0.5s, ease-out, -webkit-transform 0.5s;
	border-bottom-left-radius: 400px;
}
.hide{
	-webkit-transform:translateX(+100%);
	        transform:translateX(+100%);
}

.bar{
	position:fixed;
	z-index:200;
	top:10px;
	right:10px;
}
.bar-line{
  width: 34px;
  height: 4px;
  background-color:var(--color-one);
  margin: 8px 0;
  border-bottom: solid 2px var(--color-two);
}



.bgPolygon {
  -webkit-clip-path: polygon(50% 0%, 100% 0, 100% 90%, 50% 100%, 0 90%, 0 0);
  clip-path: polygon(50% 0%, 100% 0, 100% 90%, 50% 100%, 0 90%, 0 0);
}
.homeBorderRadius{
   border-top-right-radius:250px;
}
.link-effect{
  -webkit-animation-name: grow-effect;
  -webkit-animation-duration: 2s;
  transition: all .2s ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-delay:1s;
          animation-delay:1s;
}
.link-effect:hover{
  color:var(--color-one);
}

.link-effect:hover{
   -webkit-animation-name: none;
}
.myInitial{
  position:absolute;
  z-index:200;
  top:10px;
  left:10px;
}
.myInit{
  position:absolute;
  z-index:200;
  top:14px;
  left:10px;
}

 @-webkit-keyframes grow-effect{
 from {-webkit-transform: scale(1);transform: scale(1);}
 50%{-webkit-transform: scale(1.5);transform: scale(1.5);}
  to {-webkit-transform: scale(1);transform: scale(1);}
}

.container {
  position: relative;
  border-radius:30px;
}
.container:hover{
background:var(--color-two);
}
.container:hover .project-info{
  opacity: 1;
  transition: .5s ease-in;
}
.projectImage{
  border-radius:30px;
  /*border:solid 1px white;*/
}
.container:hover .text{
  opacity: 1;
  transition: .5s ease-in;
}

.project-info{
  position: absolute;
  top: 25%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity:0;
}

.container:hover .blurImage{
    opacity:0;
    transition: .5s ease-in; 
}
.text {
  position: absolute;
  top: 70%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  opacity:0;
  color:rgb(0,0,0,.7);
}


@media(max-width:380px){
  .projectImage{
    width:300px;
    height:230px;
  }
 
}
textarea,input:focus{
  outline:none;
}
