/**************
Link Effect
************/
a,.btnBtn {
  transition: all 500ms cubic-bezier(0.400, 0.500, 0.160, 1.300);
  -webkit-transition: all 500ms cubic-bezier(0.400, 0.500, 0.160, 1.300);
  -moz-transition: all 500ms cubic-bezier(0.400, 0.500, 0.160, 1.300);
  -ms-transition: all 500ms cubic-bezier(0.400, 0.500, 0.160, 1.300);
  -o-transition: all 500ms cubic-bezier(0.400, 0.500, 0.160, 1.300);
  border-radius:30px;
}

a.linkBtn:hover , .btnBtn:hover{
  color:#001B44;
  color:var(--color-two);
  box-shadow: #FFB700 0 0px 0px 30px inset;
  box-shadow: var(--color-one) 0 0px 0px 30px inset;
}

